'use client';
import Link from 'next/link';

export default function Header() {
    return (
        <header className="sticky top-0 z-40 backdrop-blur-xl bg-black/20 border-b border-white/10">
            <div className="container h-16 flex items-center justify-between">
                <div className="flex items-center gap-2">
                    <span className="inline-block h-3 w-3 rounded-full bg-gradient-to-r from-fuchsia-500 to-cyan-400 animate-pulse" />
                    <span className="font-semibold tracking-tight">Cybernith</span>
                    <span className="text-white/50 hidden sm:inline">— Full-Stack Developer</span>
                </div>
                <nav className="hidden md:flex items-center gap-6 text-sm text-white/80">
                    <a href="#work" className="hover:text-white">کارها</a>
                    <a href="#services" className="hover:text-white">تخصص‌ها</a>
                    <a href="#stack" className="hover:text-white">تکنولوژی</a>
                    <a href="#about" className="hover:text-white">درباره</a>
                    <a href="#contact" className="hover:text-white">تماس</a>
                </nav>
                <div className="flex items-center gap-2">
                    <Link href="/resume.pdf" className="hidden sm:block">
                        <span className="glass px-4 py-2 rounded-2xl hover:bg-white/10">دانلود رزومه</span>
                    </Link>
                    <a href="#contact" className="px-4 py-2 rounded-2xl bg-white text-black hover:bg-white/90">
                        گفتگو
                    </a>
                </div>
            </div>
        </header>
    );
}
